.background {
  height: 100vh;
  width: 100vw;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: fixed;
  z-index: -1;
  opacity: 0;
  animation: show 0.5s ease-in-out forwards;

  @keyframes show {
    100% {
      opacity: 1;
    }
  }
}
